// Breakpoints

// 600px, 900px, 1200px, 1600px
$breakpoint-up: (
  'for-tablet-portrait-up': '37.5em',
  'for-tablet-landscape-up': '56.25em',
  'for-desktop-up': '75em',
  'for-big-desktop-up': '100em',
);

// 599px, 899px, 1199px, 1599px
$breakpoint-down: (
  'for-phone-only': '37.4375em',
  'for-tablet-landscape-down': '56.1875em',
  'for-desktop-down': '74.9375em',
  'for-big-desktop-down': '99.9375em',
);

@mixin breakpoint-up($size) {
  @media (min-width: map-get($breakpoint-up, $size)) {
    @content;
  }
}

@mixin breakpoint-down($size) {
  @media (max-width: map-get($breakpoint-down, $size)) {
    @content;
  }
}
